.trends__container {
  padding-top: 40px;
  @include tablet {
    padding-top: 52px;
  }
  @include desktop {
    padding-top: 80px;
  }
}
.trends {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
  @include tablet {
    margin-bottom: 24px;
  }
  @include desktop {
    margin-bottom: 28px;
  }
  &__header {
    font-weight: 500;
    font-size: 16px;
    line-height: calc(19 / 16);
    text-transform: uppercase;
    @include tablet {
      font-size: 24px;
      line-height: calc(28 / 24);
    }
    @include desktop {
      font-size: 28px;
      line-height: calc(33 / 28);
    }
  }
  &__link {
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    line-height: calc(19 / 16);
    color: var(--accent-color);
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @include tablet {
      font-size: 24px;
      line-height: calc(28 / 24);
    }
    @include desktop {
      font-size: 28px;
      line-height: calc(33 / 28);
    }
    &:hover {
      transform: scale(1.05);
    }
  }
}
.upcoming {
  padding-top: 32px;
  padding-bottom: 40px;
  @include tablet {
    padding-bottom: 77px;
  }
  @include desktop {
    padding-bottom: 100px;
  }
  &__header {
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: calc(19 / 16);
    text-transform: uppercase;
    @include tablet {
      font-size: 24px;
      line-height: calc(28 / 24);
    }
    @include desktop {
      font-size: 28px;
      line-height: calc(33 / 28);
    }
  }
}
#trending {
  width: 100%;
  @media screen and (min-width: 768px) {
    display: flex;
  }
}
#trending li:nth-child(2),
#trending li:nth-child(3) {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.trending__item {
  position: relative;
  margin-right: 16px;
  height: 406;
  min-width: 280;
  border-radius: 5px;
  overflow: hidden;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (max-width: 767px) {
    width: 100%;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    min-width: 224;
    height: 325;
  }
  @media screen and (min-width: 1280px) {
    min-width: 395;
    height: 574;
  }
  &:hover {
    transform: scale(1.05);
    // box-shadow: 5px 5px 5px 0px #000000, 5px 5px 10px 5px #383838;
    box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, 0.42);
    cursor: pointer;
  }
  .light &:hover {
    box-shadow: 5px 5px 5px 0px #3a3939, 5px 5px 10px 5px #383838;
  }
  &:last-child {
    margin-right: 0;
  }
}
.trending__item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 53.48%, rgba(0, 0, 0, 0.9) 92.16%);
  z-index: 1;
}
.trending__img {
  position: absolute;
  object-fit: cover;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.trending__title {
  position: absolute;
  bottom: 0;
  left: 0;
  padding-left: 12px;
  padding-bottom: 42px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(20 / 12);
  color: var(--second-color);
  width: 100%;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 75%;
    padding-bottom: 52px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(23 / 20);
    width: 75%;
    padding-bottom: 40px;
  }
}
.trending__meta {
  display: flex;
  position: absolute;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(20 / 12);
  color: var(--additional-color);
  bottom: 0;
  width: 100%;
  padding-left: 12px;
  padding-bottom: 12px;
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(23 / 20);
  }
  z-index: 5;
}
.trending__genres {
  font-size: 12px;
  @include tablet {
    width: 65%;
  }
  @include desktop {
    font-size: 16px;
  }
}
.upcoming__title {
  margin-top: 24px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: calc(23 / 20);
  text-transform: uppercase;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    font-size: 28px;
    line-height: calc(33 / 28);
  }
  @media screen and (min-width: 1280px) {
    font-size: 32px;
    line-height: calc(38 / 22);
    margin-top: 0;
  }
}
.upcoming__div {
  display: flex;
  margin-bottom: 20px;
}
.upcoming__container {
  min-width: 150px;
  max-width: 330px;
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 300px;
  }
}
.upcoming__info {
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 16px;
  line-height: calc(19 / 16);
  color: var(--additional-color);
}
.upcoming__date {
  color: var(--accent-color);
}
.vote-background {
  color: var(--primary-color);
  background-color: var(--second-color);
  border-radius: 5px;
  padding: 1px 8px;
}
.upcoming__about {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: calc(19 / 16);
  color: var(--additional-color);
}
.upcoming__overview {
  margin-bottom: 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: calc(20 / 14);
  color: var(--supplemental-color);
}
.upcoming__item {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  transform: scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 1280px) {
    flex-direction: row;
  }
  &:hover {
    transform: scale(1.05);
    box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, 0.42);
    cursor: pointer;
  }
  .light &:hover {
    box-shadow: 5px 5px 5px 0px #3a3939, 5px 5px 10px 5px #383838;
  }
}
.upcoming__item picture {
  @include desktop {
    display: inline-block;
    min-width: 805px;
    height: 458px;
    margin-right: 16px;
  }
}
.upcoming__img {
  width: 100%;
  display: block;
  overflow: hidden;
  border-radius: 5px;
  @media screen and (min-width: 1280px) {
    min-width: 805px;
    height: 458px;
  }
}
#btn__upcoming {
  margin-right: 5px;
  transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1), border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    color: var(--accent-color);
    border: solid 1px var(--accent-color);
    background: var(--second-color);
  }
}
.film-card__rating {
  position: absolute;
  right: 12px;
  bottom: 15px;
  display: inline-block;
}

//!=================
.rating {
  display: flex;
  align-items: flex-end;
  font-size: 20px;
  line-height: 0.75;
  @include tablet {
    font-size: 15px;
  }
  @include desktop {
    font-size: 25px;
  }
}
.rating__body {
  position: relative;
}
.rating__body::before {
  content: '★★★★★';
  display: block;
}
.rating__active {
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}
.rating__active::before {
  content: '★★★★★';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(175.22deg, #f84119 9.4%, rgba(248, 159, 25, 0.68) 91.91%);
  // -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.rating__items {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.rating__item {
  flex: 0 0 20%;
  min-width: 90px;
  height: 100%;
  opacity: 0;

  @include tablet {
    min-width: 66px;
  }
}
.rating__value {
  font-size: 50%;
  line-height: 1;
  padding: 0px 0px 0px 10px;
}
.disabled {
  pointer-events: none;
  cursor: not-allowed;
  opacity: 0.5;
}
